import './Login.css';
import { useState, useEffect } from "react";
import Axios from "axios";
import React from "react";
import {motion} from "framer-motion"



const Login = () => {
  useEffect(() => {
    showProject();
  },[]);
    const [projektyList, setProjektyList] = useState([]);
    const [id, setID]= useState(0);
    const showProject = () => {
    
        Axios.get('http://localhost:3001/Sprojekty', {
    
        }).then((response) => {
          setProjektyList(response.data);
          console.log(response);
        })
      } 

      const Delete = (id) => {
    
        Axios.delete(`http://localhost:3001/delete/${id}`, {
     
        
      } )}
      
    return(
     <div className="seznam-projek">
       <table className="tabulka">
       {projektyList.map((val, key) => {
        
        return <div className="list"> 
        <tr className="table-radek">
        <h3>Nazev:</h3>
          <td className="table-sloupec"><h3 className="table-nazev">{val.Nazev}  </h3></td>
          <motion.i whileHover={{scale:1.8, cursor:"pointer"}} onClick={() => {Delete(val.ID)}}  className="ikony fas fa-trash" />
          <motion.i whileHover={{scale:1.8, cursor:"pointer"}} className="ikony fas fa-pencil-alt" />
          
        </tr>  
        
        </div>
        
        
      })}
            
            </table> 

</div>

    )
}

export default Login;