import './Add.css';
import { useState, useEffect } from "react";
import Axios from "axios";
import React from "react";
import {motion} from "framer-motion"



const Add = () => {
    const [nazev, setNazev] = useState('');
    const [obsah, setObsah] = useState('');
    const [auto, setAuto] = useState('');
    const [img1, setImg1] = useState('');
    const [img2, setImg2] = useState('');
    const [img3, setImg3] = useState('');
    const [img4, setImg4] = useState('');
    const [img5, setImg5] = useState('');

    const posli = () => {
     Img_upload()
     Img_upload2()
     Img_upload3()
     Img_upload4()
     Img_upload5()
      addProjekt()
    }

    const Img_upload = () => {
      const data = new FormData();
      data.append("file", img1);
      data.append("Name", nazev);
      data.append("Auto", auto);
     
      Axios.post(`http://localhost:3001/img`, data).then((response)=> {
          console.log("response")
      }).catch(function(error) {
        console.log(error);
      });
    }
    const Img_upload2 = () => {
      const data = new FormData();
      data.append("file", img2);
      data.append("Name", nazev);
      data.append("Auto", auto);
     
      Axios.post(`http://localhost:3001/img2`, data).then((response)=> {
          console.log("response")
      }).catch(function(error) {
        console.log(error);
      });
    }
    const Img_upload3 = () => {
      const data = new FormData();
      data.append("file", img3);
      data.append("Name", nazev);
      data.append("Auto", auto);
     
      Axios.post(`http://localhost:3001/img3`, data).then((response)=> {
          console.log("response")
      }).catch(function(error) {
        console.log(error);
      });
    }

    const Img_upload4 = () => {
      const data = new FormData();
      data.append("file", img4);
      data.append("Name", nazev);
      data.append("Auto", auto);
     
      Axios.post(`http://localhost:3001/img4`, data).then((response)=> {
          console.log("response")
      }).catch(function(error) {
        console.log(error);
      });
    }
    
    const Img_upload5 = () => {
      const data = new FormData();
      data.append("file", img5);
      data.append("Name", nazev);
      data.append("Auto", auto);
     
      Axios.post(`http://localhost:3001/img5`, data).then((response)=> {
          console.log("response")
      }).catch(function(error) {
        console.log(error);
      });
    }


    const addProjekt = () => {
      
      
        Axios.post('http://localhost:3001/create', {
          nazev: nazev,
          obsah: obsah,
          auto: auto,
          img1: img1,
          img2: img2,
          img3: img3,
          img4: img4,
          img5: img5,
          
        })
        
      };
      
    return(
     <div className="pridat">
         
             <h2>Přidat příspěvek</h2>
             <label>Název</label><br/>
             <input onChange = {(event => {setNazev(event.target.value)})} className="pridat-input" type="text"></input><br/>
             <label>Auto</label><br/>
             <input onChange = {(event => {setAuto(event.target.value)})} className="pridat-input" type="text" ></input><br/>
             <label>Obsah</label><br/>
             <textarea onChange = {(event => {setObsah(event.target.value)})}></textarea><br/>
             <input onChange = {(event) => {const file = event.target.files[0];
                                            if(file == undefined ){
                                              setImg1('')
                                             
                                            }else{
                                              
                                              setImg1(file.name)
                                              
                                            }
                                                                                     
            }}  type="file">
        
             </input>

             <input onChange = {(event) => {const file = event.target.files[0];
                                 if(file == undefined ){
                                  setImg2('')
                                 
                                }else{
                                  
                                  setImg2(file.name)
                                  
                                }
                                                                                     
            }}  type="file">
        
             </input>

             <input onChange = {(event) => {const file = event.target.files[0];
              if(file == undefined ){
                setImg3('')
                
              }else{
                
                setImg3(file.name)
                
              }
                                                                                     
            }}  type="file">
        
             </input>

             <input onChange = {(event) => {const file = event.target.files[0];
              if(file == undefined ){
                setImg4('')
                
              }else{
                
                setImg4(file.name)
                
              }
                                                                                     
            }}  type="file">
        
             </input>

             <input onChange = {(event) => {const file = event.target.files[0];
              if(file == undefined ){
                setImg5('')
                
              }else{
                
                setImg5(file.name)
                
              }
                                                                                     
            }}  type="file">
        
             </input>


             


             <button onClick = {posli} className="pridat-butt"> Přidat</button>
         
     </div>

    )
}

export default Add;