import "./Kontakt.css"
import emailjs from 'emailjs-com';
import {motion} from "framer-motion"


const Kontakt = () => {

      function sendEmail(e) {
        e.preventDefault();
    
        emailjs.sendForm('service_er8yhzb', 'template_gstlyp6', e.target, 'user_ZbGjJzjZHxWAhXWeZ4ECQ')
          .then((result) => {
              console.log(result.text);
          }, (error) => {
              console.log(error.text);
          });
          e.target.reset();
        }
        const Goto = () =>{
            window.location.assign("https://www.facebook.com/ServisElektroHavirov")
        };
        const Gotoig = () =>{
            window.location.assign("https://www.instagram.com/koncern_servis/")
        };
    return(
        <motion.div className="kontakt-page" initial={{opacity:0}} animate={{opacity:1}} transition={{duration:1.5}}> 
        <h1>Kontakt</h1>
        

        <div className="udaje">
            <div>
            <h2>Kde nás najdete:</h2>
            <table className="tabulka-cont">
                <tr>
                    <td>Adresa:</td>
                    <td className="hodiny">Těrlická 1198,</td>
                </tr>   
                <tr>
                    <td></td>
                    <td className="hodiny">735 35 Horní Suchá</td>
                </tr> 
                <tr>
                    <td>Telefon:</td>
                    <td className="hodiny">776 033 609</td>
                </tr> 
                <tr>
                    <td>Email:</td>
                    <td className="hodiny">koncernservis@email.cz</td>
                </tr> 
                <tr>
                    <td>Instagram:</td>
                    <motion.td whileHover={{cursor:"pointer"}}  onClick={Gotoig} className="hodiny">koncernservis-IG</motion.td>
                </tr> 
                <tr>
                    <td>Facebook:</td>
                    <motion.td whileHover={{cursor:"pointer"}} onClick={Goto} className="hodiny">koncernservis-FB</motion.td>
                </tr> 
                </table>
                </div>
                <div className="map"><iframe className="mapa" frameborder="0" scrolling="no" marginheight="0" marginwidth="0" src="https://maps.google.com/maps?width=100%25&amp;height=600&amp;hl=en&amp;q=T%C4%9Brlick%C3%A1%201198+(Koncern%20servis)&amp;t=&amp;z=16&amp;ie=UTF8&amp;iwloc=B&amp;output=embed"></iframe></div>
                <br/>
                
        </div>
        <div className="map-mobile"><iframe className="mapa" frameborder="0" scrolling="no" marginheight="0" marginwidth="0" src="https://maps.google.com/maps?width=100%25&amp;height=600&amp;hl=en&amp;q=T%C4%9Brlick%C3%A1%201198+(Koncern%20servis)&amp;t=&amp;z=16&amp;ie=UTF8&amp;iwloc=B&amp;output=embed"></iframe></div>
        
    <form className="contact-form" onSubmit={sendEmail} >
            <h2>
                Napište Nám
            </h2>
      
      <label>Jméno a příjmení:</label>
      <br />
      <input  type="text" name="from_name" className="inputs"/>
      <br />
      <label>Email:</label>
      <br />
      <input type="email" name="from_email"  className="inputs"/>
      <br />
      <label>Zpráva:</label>
      <br />
      <textarea name="message" className="zprava" cols="50" rows="15"/>
      <br />
      <motion.input whileHover={{scale:1.2}} type="submit" value="Poslat"  className="poslat"/>
    </form>
    
        </motion.div>
    )
}
export default Kontakt;