import './Nav.css';
import {Link} from "react-router-dom";
import { useState } from "react";
import { animate, motion} from "framer-motion"

const Nav = () => {
    const [state, setState] = useState(true);
    const handle = () =>  setState(!state);
    const close = () => setState(true);
    return(
        <nav>
        <div className="Nav">
            <h2 className="nadpis"> Servis</h2>
            <div onClick = {handle} className={"icon"}>
            <i  className={state ? "fas fa-bars" : "fas fa-times"}></i>
            </div>
            
            <ul className="list-hidden">
                <Link className="link"  to="/" >
                <li>Domů</li>
                </Link>
                <Link className="link" to="/sluzby">
                <li>Služby</li>
                </Link>
                <Link className="link" to="/Projekty">
                <li>Projekty</li>
                </Link>
                <Link className="link" to="/kontakt">
                <li>Kontakt</li>
                </Link>
            </ul>
        </div>

        <motion.div  className="list-show">
        <ul className={state ? "ul-hide" : "ul-show"}>
                <Link className="link" onClick = {close} to="/" >
                <li>Domů</li>
                </Link>
                <Link className="link" onClick = {close} to="/sluzby">
                <li>Služby</li>
                </Link>
                <Link className="link" onClick = {close} to="/projekty">
                <li>Projekty</li>
                </Link>
                <Link className="link" onClick = {close} to="/kontakt">
                <li>Kontakt</li>
                </Link>
            </ul>
        </motion.div>
        </nav>
        
    )
}

export default Nav;