import './Landing.css';
import React from "react";
import jedna from './images/car.png'
import { animate, motion} from "framer-motion"
import {Link} from "react-router-dom";


const Landing = () => {
    
    return(
     
         
<div className="landing">
<div className="intro">
    <motion.div initial={{opacity:0}} animate={{opacity:1}} transition={{duration:1.5}} className="text">
       <h1>Koncern Servis</h1>
       
           <motion.h3 whileHover={{x:12}}  className="odraz"> <i className="fas fa-tools" />  Zaměřujeme se na servis koncernových značek vozidel.</motion.h3>
           
           <motion.h3 whileHover={{x:12}} className="odraz"><i className="fas fa-car" />  Provádíme generální opravy motorů.</motion.h3>
           
           <motion.h3 whileHover={{x:12}} className="odraz"><i className="fas fa-flag-checkered" />  Děláme individuální ůpravy vozidel.</motion.h3>
           
           <motion.h3 whileHover={{x:12}} className="odraz"><i className="fas fa-info" />    A mnoho dalšího.</motion.h3>
           
           <Link  to="/sluzby"><motion.button className="zjisti" whileHover={{scale:1.1}}>Zjsitit více</motion.button></Link>
       
    </motion.div>
    
    <motion.img src={jedna} className="sup" initial={{x:1000, opacity:0}} animate={{x:0, opacity:1}}  transition={{duration:1.7}}/>
    
</div>
</div>     




    )
}

export default Landing;