import './Footer.css';
import {Link} from "react-router-dom";
import { useState } from "react";
import { animate, motion} from "framer-motion"
import skoda from './images/loga/skoda.png'
import seat from './images/loga/seat.png'
import vw from './images/loga/vw.png'
import audi from './images/loga/audi.png'
import face from './images/loga/face.png'
import insta from './images/loga/insta.png'

const Footer = () => {
    const Goto = () =>{
        window.location.assign("https://www.facebook.com/ServisElektroHavirov")
    };
    const Gotoig = () =>{
        window.location.assign("https://www.instagram.com/koncern_servis/")
    };
    return(
        
        <footer>
            <div className="kontakt">
                <h3 className="kont">Kontakt</h3>
                <table className="tabulka-kontakt">
                <tr>
                    <td>Adresa:</td>
                    <td className="hodiny">Těrlická 1198,</td>
                </tr>   
                <tr>
                    <td></td>
                    <td className="hodiny">735 35 Horní Suchá</td>
                </tr> 
                <tr>
                    <td>Telefon:</td>
                    <td className="hodiny">776 033 609</td>
                </tr> 
                <tr>
                    <td>Email:</td>
                    <td className="hodiny">koncernservis@email.cz</td>
                </tr> 
                </table>
            </div>
            <div className="soc">
            <h3 className="soc-nadpis">Sociální sítě</h3>
            
            <div className="icons">
            <motion.img onClick={Goto} className="fb-ig" whileHover={{scale:1.3}} src={face}></motion.img>
            <motion.img onClick={Gotoig} className="fb-ig" whileHover={{scale:1.2}} src={insta}></motion.img>
            </div>
            <div className="soc-obr">
            <img className="logo vw" src={vw}></img>
            
            <img className="logo" src={audi}></img>
            <img className="logo" src={seat}></img>
            <img className="skoda logo" src={skoda}></img>
            </div>
            </div>
            <div className="otvr">
            <h3>Otevírací hodiny</h3>
            <table>
                <tr>
                    <td>Pondělí:</td>
                    <td className="hodiny">9:00-18:00</td>
                </tr>
                <tr>
                    <td>Úterý:</td>
                    <td className="hodiny">9:00-18:00</td>
                </tr>
                <tr>
                    <td>Středa:</td>
                    <td className="hodiny">9:00-18:00</td>
                </tr>
                <tr>
                    <td>Čtvrtek:</td>
                    <td className="hodiny">9:00-18:00</td>
                </tr>
                <tr>
                    <td>Pátek:</td>
                    <td className="hodiny">9:00-18:00</td>
                </tr>
                <tr>
                    <td>Sobota:</td>
                    <td className="hodiny">Zavřeno</td>
                </tr>
                <tr>
                    <td>Neděle:</td>
                    <td className="hodiny">Zavřeno</td>
                </tr>
            </table>
            </div>
        </footer>
    )
}

export default Footer;