import './Projekty.css';
import { useState, useEffect } from "react";
import Axios from "axios";
import React from "react";
import dva from './images/car.png';
import { animate, motion} from "framer-motion"
import {Link} from "react-router-dom";

const Projekty = () => {
  useEffect(() => {
    showProject();
  },[]);
    const [projektyList, setProjektyList] = useState([]);
    const [obr1, setObr1]= useState(false);
    const showProject = () => {
    
        Axios.get('http://localhost:3001/Sprojekty', {
    
        }).then((response) => {
          setProjektyList(response.data);
          
          
        })
      } 
      ;
      const Goto = () =>{
        window.location.assign("https://www.facebook.com/ServisElektroHavirov")
    };
    const Gotoig = () =>{
        window.location.assign("https://www.instagram.com/koncern_servis/")
    };

    
    return(
     <div className="projekty">
       
       <h1 className="projekty-nadpis">Naše Projekty</h1>
            {projektyList.slice(0).reverse().map((val, key) => {
             
        return <div className="projekt"> 
          <div className="vypis">
          <h1 className="nadpis">{val.Nazev} </h1>
          <h3 className="auto">{val.Auto}</h3>
          <p className="obsah">{val.Text}</p>
          </div>
          <img src={'/images/'+ val.Img1} className="img-projekty" onError={i => i.target.style.display='none'}></img>
          <img src={'/images/'+ val.Img2} className="img-projekty" onError={i => i.target.style.display='none'}></img>
          <img src={'/images/'+ val.Img3} className="img-projekty" onError={i => i.target.style.display='none'}></img>
          <img src={'/images/'+ val.Img4} className="img-projekty" onError={i => i.target.style.display='none'}></img>
          <img src={'/images/'+ val.Img5} className="img-projekty" onError={i => i.target.style.display='none'}></img>
          
        
        </div>
        

      })}
            
            <p className="projekty-nadpis dalsi_projekty">Naše další projekty naleznete na našem-- <motion.b onClick={Goto} whileHover={{cursor:"pointer"}}>Facebook</motion.b > / <motion.b onClick={Gotoig} whileHover={{cursor:"pointer"}}>Instagramu</motion.b></p>

</div>

    )
}

export default Projekty;